@import url('../styles/main.css');

.person-card-container {
  width: 248px;
  display: inline-block;
  border: 0.6px solid var(--color-gray-border);
  border-top-right-radius: 2px;
  border-top-left-radius: 2px;
  margin: 5px;
  position: relative;
  background-color: var(--color-dark-medium);
}

.person-card-header {
  background-color: var(--color-dark-medium);
  display: flex;
  padding: 20px var(--padding-left);
}

.person-card-user-icon {
  width: 53px;
  height: 70px;
  background-repeat: no-repeat;
  background-position: 0 0;

  background-image: url('data:image/png;base64,');
}

.person-card-user-meta {
  padding-left: var(--padding-left);
}

.person-card-title {
  color: var(--color-yellow);
  font-size: var(--font-size-small);
}

.person-card-main-title {
  color: var(--color-white);
  font-size: var(--font-size-medium);
}

.person-card-main-sub-title {
  color: var(--color-white);
  font-size: var(--font-size-medium);
}

.person-card-body {
  padding: var(--padding-top) var(--padding-left);
  background-color: var(--color-white);
}

.person-card-body:hover {
  box-shadow: 0 1px 4px 0 var(--color-black);
}
